<template>
    <div>
        <button class="title-btn" @click="isOpen=!isOpen">{{group.name}}</button>
        <transition name="slider">
            <ul v-show="isOpen" :style="ulStyle">
                <li v-for="f in group.list">{{f}}</li>
            </ul>
        </transition>
    </div>
</template>
<script>
export default {
    props: ['group'],
    data: function () {
        return {
            isOpen: true
        }
    },
    computed: {
        ulStyle: function () {
            var h = this.group.list.length * 22 + 'px'
            return {
                height: h
            }
        }
    }

}
</script>
<style>
.title-btn {
    width: 100%;
    height: 30px;
}

.slider-enter {
    height: 0px !important;
}

.slider-enter-active {
    transition: all 0.7s linear;
    overflow: hidden;
}


.slider-leave-active {
    /*transform-origin: 50% 0;
            transform: scale(1,0);*/
    height: 0 !important;
    transition: all 0.7s linear;
    overflow: hidden;
}
</style>




