<template>
    <div class="slider-box" @mousedown="mousedown">
        <div class='slider-line'></div>
        <div class='slider-point' :style="pointStyle"></div>
    </div>
</template>
<script>
export default {
    props: {
        volumn: {
            default: 0.5,
            type: Number,
            // validator: function (v) {
            //     return v >= 0 && v <= 1
            // }
        }
    },
    data: function () {
        return {
            left: 360 * this.volumn + 20,
            isDown: false
        }
    },
    computed: {

        // left:function(){
        //     return 360 * this.volumn + 20
        // },
        pointStyle: function () {
            console.log(123)
            this.left = this.left < 20 ? 20 : this.left
            this.left = this.left > 380 ? 380 : this.left
            return {
                left: this.left - 10 + 'px'
            }
        }
    },
    methods: {
        mousedown: function (e) {
            // 组件对象的$el表示组件的根元素
            console.log(this.$el)
            var x = e.pageX - this.$el.offsetLeft
            this.left = x
            this.isDown = true
        },
        mousemove: function (e) {
            if (this.isDown) {
                var x = e.pageX - this.$el.offsetLeft
                this.left = x
            }
        },
        mouseup: function () {
            this.isDown = false
        },
    },
    mounted: function () {
        window.addEventListener('mousemove', this.mousemove.bind(this))
        window.addEventListener('mouseup', this.mouseup.bind(this))
        // window.onmousemove = this.mousemove.bind(this)
        // window.onmouseup = this.mouseup.bind(this)
    },
    watch: {
        left: function () {
            this.$emit('volchange', (this.left - 20) / 360)
        }
    }



}
</script>
<style>
.slider-box {
    border: solid 1px black;
    position: relative;
    height: 40px;
    width: 400px;
}

.slider-line {
    position: absolute;
    width: 360px;
    height: 6px;
    background-color: orangered;
    left: 20px;
    top: 17px;
}

.slider-point {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: radial-gradient(white, black);
    /*left: 100px;*/
    top: 10px;
}
</style>




