import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/page/Hello'
import Friends from '../page/Friends.vue'
import Star from '../page/Star.vue'
import Slider from '../page/Slider.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path:'/friend',
      component:Friends
    },
    {
      path:'/star',
      component:Star
    },
    {
      path:'/slider',
      component:Slider
    },
  ]
})



