<!--
    .vue文件是vue项目中的组件文件，一个.vue组件就是一个组件
    .vue文件内容的三大部分，template script style
    template表示组件的模板
    script中需要导出的对象
    style中是组件的css样式
 -->

``<template>
  <div id="app">
    <div class="tab-bar clear-fix">
        <div class="tab-item">
          <router-link to="/">首页</router-link>
        </div>
        <div class="tab-item">
          <router-link to="/friend">好友</router-link>
        </div>
        <div class="tab-item">
          <router-link to="/star">评分</router-link>
        </div>
        <div class="tab-item">
          <router-link to="/slider">滑块</router-link>
        </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
}
</script>

<style scoped>
    .tab-bar{
    position:fixed;
    bottom:0;
    width:100%;
  }                                                                            
  .clear-fix:after{
    content:'';
    display:block;
    clear:both;
  }
  .tab-item{
    width:25%;
    height:57px;
    border:solid 1px #000;
    text-align:center;
    line-height:57px;
    float:left;
  }
</style>



