<template>
    <div id="app">
        <FriendList :friends="friends"></FriendList>
    </div>
</template>

<script>
// 在一个组件文件中，要使用另外一个组件，要先使用，后注册
import FriendList from '../components/FriendList.vue'
export default {
    data: function () {
        return {
            friends: [{
                name: '家人',
                list: ['爸爸', '妈妈', '姐姐', '弟弟']
            }, {
                name: '同学',
                list: ['史帅超', '陈建明', '游超杰', '李刚', '王文龙', '赵维光']
            }, {
                name: '朋友',
                list: ['帅帅', '狙击手', '皮皮虾', '超姐', '龙龙']
            }]
        }
    },
    components:{
        FriendList
    }
}
</script>

<style scope>

</style>




