<template>
    <div id="app">
        <Slider :volumn="volumn" @volchange="volchange"></Slider>
        <p>当前音量：{{volumn}}</p>
        <input v-model.number="volumn">
    </div>
</template>
<script>
import Slider from '../components/Slidermy'
export default {
    data() {
        return {
            volumn: 0.5,
        }
    },
    methods: {
        volchange: function (e) {
            this.volumn = e
        }
    },
    components:{
        Slider
    }
}
</script>
<style>
</style>




